import React, { Component } from "react";

import { Button, Popconfirm, Tooltip, Form } from "antd";
import EditableCell from "./editableCell";
import EditableContext from "./editableContext";
import Scrollbars from "../../../../components/utility/customScrollBar";
import TableWrapper from "../../../Tables/antTables/antTable.style";
import "../../../App/editableStyle.css";
import IntlMessages from "../../../../components/utility/intlMessages";
import CardWrapper from "../../../App/app.style";
import ModalItemDetail from "./itemDetailModal";
import { getUnitConversion } from "../../../../helpers/helper";
import NumberFormat from "../../../App/NumberFormat";
import getLanguage from "../../../App/MyLanguange";

const EditableRow = ({ form, index, ...props }) => (
  <EditableContext.Provider value={form}>
    <tr {...props} />
  </EditableContext.Provider>
);

const EditableFormRow = Form.create()(EditableRow);

// const getTotal = arr => {
//   let totalQty = 0;
//   arr.map(data => {
//     totalQty += parseFloat(data.base_qty);
//   });

//   return totalQty;
// };

class EditableTable extends Component {
  constructor(props) {
    super(props);
    const { items } = props;

    this.state = {
      dataSource: [...items],
      count: items.length + 1,
      totalQty: 0,
      visible: false,
      selectedItem: {}
    };
  }

  componentDidUpdate() {
    const {
      loadingTransactions,
      loadingItems,
      item,
      items,
      clearItem,
      clearItems,
      handleSaveItemList,
      clear,
      status
    } = this.props;

    if (!loadingTransactions && !loadingItems && item) {
      const { dataSource } = this.state;
      const { count, totalQty } = this.state;
      // console.log(item.length);
      const find = dataSource.findIndex(
        currData => currData.id_item === item.id_item
      );
      // console.log(find);

      if (find < 0) {
        const preference_item = item.preference_item;
        let newData = Object.assign(
          {},
          {
            index_no: count,
            id_item: item.id_item,
            item_code: item.item_code,
            item_name: item.item_name,
            item_type: item.item_type,
            current_qty: item.current_qty ? parseFloat(item.current_qty) : 0,
            preference_item: {
              max_stock: preference_item.max_stock,
              min_stock: preference_item.min_stock,
              reorder_point: preference_item.reorder_point
            },
            id_unit: preference_item.purchase_unit.id_unit,
            unit_code: preference_item.purchase_unit.unit_code,
            id_base_unit: item.base_unit.id_unit,
            base_unit_code: item.base_unit.unit_code,
            unit_conversion: item.unit_conversion,
            unit_conv: getUnitConversion(
              preference_item.purchase_unit.id_unit,
              item.unit_conversion
            ),
            request_qty: preference_item.purchase_qty
              ? parseFloat(preference_item.purchase_qty)
              : 1,
            base_qty: 1,
            sent_qty: 0,
            ordered_qty: 0,
            item_note: null,
            id_department: null,
            id_project: null
          }
        );

        newData.base_qty = newData.request_qty * newData.unit_conv;

        // console.log(newData);

        this.setState({
          dataSource: [...dataSource, newData],
          count: count + 1,
          totalQty: totalQty + newData.base_qty
        });

        clearItem([...dataSource, newData], totalQty + newData.base_qty);
        // console.log(this.props.item);
      } else if (find >= 0) {
        let newTotQty = 0;

        const updateData = dataSource.filter(
          currData => currData.index_no !== count
        );

        let newData = Object.assign({}, updateData[find]);
        newData.request_qty = parseFloat(newData.request_qty) + 1;
        newData.base_qty = newData.request_qty * newData.unit_conv;

        updateData.splice(find, 1, {
          ...updateData[find],
          ...newData
        });
        updateData.map(
          currData => (newTotQty += parseFloat(currData.base_qty))
        );

        this.setState({
          dataSource: [...updateData],
          totalQty: newTotQty
        });

        clearItem([...updateData], newTotQty);
        // console.log(this.props.item);
      }
    } else if (!loadingTransactions && !loadingItems && items.length !== 0) {
      let newTotQty = 0;
      let newIndex = 0;
      let newData = [];

      items.map(item => {
        item.preference_item = item.preference_item
          ? item.preference_item
          : item.item.preference_item;
        item.unit_conversion = item.unit_conversion
          ? item.unit_conversion
          : item.item.unit_conversion;

        let init = {
          index_no: parseFloat(item.index_no),
          id_item: item.id_item,
          item_code: item.item_code,
          item_name: item.item_name,
          item_type: item.item.item_type,
          current_qty: item.current_qty ? parseFloat(item.current_qty) : 0,
          preference_item: status === 1 ? {
            max_stock: item.preference_item.max_stock,
            min_stock: item.preference_item.min_stock,
            reorder_point: item.preference_item.reorder_point
          } : {
            max_stock: item.max_stock,
            min_stock: item.min_stock,
            reorder_point: item.reorder_point
          },
          id_unit: item.id_unit,
          unit_code: item.unit ? item.unit.unit_code : item.unit_code,
          id_base_unit: item.item.base_unit.id_unit,
          base_unit_code: item.item.base_unit.unit_code,
          unit_conversion: item.unit_conversion,
          unit_conv: getUnitConversion(item.id_unit, item.unit_conversion),
          request_qty: parseFloat(item.request_qty),
          base_qty: parseFloat(item.base_qty),
          sent_qty: parseFloat(item.sent_qty),
          ordered_qty: parseFloat(item.ordered_qty),
          item_note: item.item_note,
          id_department: item.id_department,
          id_project: item.id_project
        };

        init.base_qty = init.request_qty * init.unit_conv;
        newData.push(init);

        newTotQty += parseFloat(item.base_qty);
        newIndex = parseInt(item.index_no) + 1;
      });

      this.setState({
        dataSource: [...newData],
        totalQty: newTotQty,
        count: newIndex
      });

      clearItems(newData, newTotQty);
    } else if (clear) {
      this.setState({ dataSource: [], count: 1, totalQty: 0 });
      handleSaveItemList([], 0);
    }
  }

  handleItemDetailModal = row => {
    const { visible } = this.state;
    visible
      ? this.setState({ visible: false, selectedItem: {} })
      : this.setState({ visible: true, selectedItem: row });
  };

  handleItemDetail = value => {
    this.setState({ visible: false, selectedItem: {} });
    this.handleSave(value);
  };

  handleDelete = index_no => {
    const { handleSaveItemList } = this.props;
    let newTotQty = 0;

    const dataSource = this.state.dataSource;

    let newData = dataSource.filter(currData => currData.index_no !== index_no);
    newData.map((currData, index) => {
      currData.index_no = index + 1;
      newTotQty += parseFloat(currData.base_qty);
    });

    this.setState({
      dataSource: [...newData],
      count: newData.length + 1,
      totalQty: newTotQty
    });
    handleSaveItemList(newData, newTotQty);
  };

  handleSave = row => {
    const { dataSource } = this.state;
    const { handleSaveItemList } = this.props;
    const newData = dataSource;

    const index = newData.findIndex(
      currData => row.id_item === currData.id_item
    );

    row.unit_conv = getUnitConversion(row.id_unit, row.unit_conversion);
    row.base_qty = row.request_qty * row.unit_conv;

    const newTotQty =
      this.state.totalQty +
      parseFloat(row.base_qty) -
      parseFloat(newData[index].base_qty);

    newData.splice(index, 1, {
      ...newData[index],
      ...row
    });

    this.setState({ dataSource: [...newData], totalQty: newTotQty });
    handleSaveItemList(newData, newTotQty);
  };

  render() {
    // console.log(this.state.dataSource);

    const { dataSource, visible, selectedItem, totalQty } = this.state;
    const components = {
      body: {
        row: EditableFormRow,
        cell: EditableCell
      }
    };

    const columns = [
      {
        title: getLanguage({ id: "no." }),
        dataIndex: "index_no",
        width: "5%",
        align: "right",
        render: text => {
          if (isNaN(text)) {
            return {
              children: text,
              props: {
                colSpan: 10,
                align: "right"
              }
            };
          } else {
            return {
              children: text,
              props: {
                colSpan: 1
              }
            };
          }
        }
      },
      {
        title: getLanguage({ id: "code" }),
        dataIndex: "item_code",
        width: "20%",
        render: (text, row) => {
          if (isNaN(row.index_no)) {
            return {
              props: {
                colSpan: 0
              }
            };
          } else if (text && text.length > 10) {
            return (
              <Tooltip placement="bottom" title={text}>
                {text.substring(0, 10) + "..."}
              </Tooltip>
            );
          } else {
            return text;
          }
        }
      },
      {
        title: getLanguage({ id: "itemName"}),
        dataIndex: "item_name",
        width: "30%",
        render: (text, row) => {
          if (isNaN(row.index_no)) {
            return {
              props: {
                colSpan: 0
              }
            };
          } else if (text && text.length > 25) {
            return (
              <Tooltip placement="bottom" title={text}>
                {text.substring(0, 25) + "..."}
              </Tooltip>
            );
          } else {
            return text;
          }
        }
      },
      {
        title: (
          <Tooltip placement="top" title={<IntlMessages id="quantityOnHand" />}>
            <a href="javascript:;"><IntlMessages id="qoh" /> </a>
          </Tooltip>
        ),
        dataIndex: "current_qty",
        width: "15%",
        align: "center",
        render: (text, row) => {
          if (isNaN(row.index_no)) {
            return {
              props: {
                colSpan: 0
              }
            };
          }

          return <NumberFormat value={text} />
        }
      },
      {
        title: (
          <Tooltip placement="top" title={<IntlMessages id="minimumStock" />}>
            <a href="javascript:;"><IntlMessages id="min" /> </a>
          </Tooltip>
        ),
        dataIndex: "preference_item.min_stock",
        width: "15%",
        align: "center",
        render: (text, row) => {
          if (isNaN(row.index_no)) {
            return {
              props: {
                colSpan: 0
              }
            };
          }

          return <NumberFormat value={text} />
        }
      },
      {
        title: (
          <Tooltip placement="top" title={<IntlMessages id="maximumStock" />}>
            <a href="javascript:;"><IntlMessages id="max" /> </a>
          </Tooltip>
        ),
        dataIndex: "preference_item.max_stock",
        width: "15%",
        align: "center",
        render: (text, row) => {
          if (isNaN(row.index_no)) {
            return {
              props: {
                colSpan: 0
              }
            };
          }

          return text && row.item_type === 1
            ? <NumberFormat value={text} />
            : "-";
        }
      },
      {
        title: (
          <Tooltip placement="top" title={<IntlMessages id="reorderPoint" />}>
            <a href="javascript:;"><IntlMessages id="rop" /> </a>
          </Tooltip>
        ),
        dataIndex: "preference_item.reorder_point",
        width: "15%",
        align: "center",
        render: (text, row) => {
          if (isNaN(row.index_no)) {
            return {
              props: {
                colSpan: 0
              }
            };
          }

          return text && row.item_type === 1
            ? <NumberFormat value={text} />
            : "-";
        }
      },
      {
        title: (
          <Tooltip placement="top" title={<IntlMessages id="sentQuantity" />}>
            <a href="javascript:;"><IntlMessages id="sent" /> </a>
          </Tooltip>
        ),
        dataIndex: "sent_qty",
        width: "15%",
        align: "center",
        render: (text, row) => {
          if (isNaN(row.index_no)) {
            return {
              props: {
                colSpan: 0
              }
            };
          }

          return <NumberFormat value={text} />
        }
      },
      {
        title: (
          <Tooltip placement="top" title={<IntlMessages id="orderedQuantity" />}>
            <a href="javascript:;"><IntlMessages id="ordered" /> </a>
          </Tooltip>
        ),
        dataIndex: "ordered_qty",
        width: "15%",
        align: "center",
        render: (text, row) => {
          if (isNaN(row.index_no)) {
            return {
              props: {
                colSpan: 0
              }
            };
          }

          return <NumberFormat value={text} />
        }
      },
      {
        title: getLanguage({ id: "bsUnt" }),
        width: "10%",
        dataIndex: "base_unit_code",
        align: "center",
        render: (text, row) => {
          if (isNaN(row.index_no)) {
            return {
              props: {
                colSpan: 0
              }
            };
          }

          return text;
        }
      },
      {
        title: (
          <Tooltip placement="top" title={<IntlMessages id="requestQuantity" />}>
            <a href="javascript:;"><IntlMessages id="request" /> </a>
          </Tooltip>
        ),
        dataIndex: "request_qty",
        width: "15%",
        // editable: true,
        align: "center",
        onCell: row => {
          if (!isNaN(row.index_no)) {
            if (
              !this.props.transaction_status ||
              this.props.transaction_status === 1
            ) {
              return {
                record: row,
                dataIndex: "request_qty",
                editable: true,
                handleSave: this.handleSave,
                colSpan: 1
              };
            } else {
              return {
                record: row,
                dataIndex: "request_qty",
                colSpan: 1
              };
            }
          } else {
            return {
              colSpan: 1
            };
          }
        }
      },
      {
        title: getLanguage({ id: "unit" }),
        width: "10%",
        dataIndex: "unit_code",
        align: "center",
        onCell: row => {
          if (!isNaN(row.index_no)) {
            if (
              !this.props.transaction_status ||
              this.props.transaction_status === 1
            ) {
              return {
                record: row,
                dataIndex: "unit_code",
                editable: true,
                handleSave: this.handleSave,
                colSpan: 1
              };
            } else {
              return {
                record: row,
                dataIndex: "unit_code",
                colSpan: 1
              };
            }
          } else {
            return {
              colSpan: 3
            };
          }
        }
      },
      {
        title: (
          <Tooltip placement="top" title={<IntlMessages id="baseQuantity" />}>
            <a href="javascript:;"><IntlMessages id="baseQty" /> </a>
          </Tooltip>
        ),
        width: "15%",
        dataIndex: "base_qty",
        align: "right",
        render: (text, row) => {
          if (isNaN(row.index_no)) {
            return {
              props: {
                colSpan: 0
              }
            };
          }

          return <><NumberFormat value={text} /> {row.base_unit_code} </>;

        }
      },
      {
        title: "",
        dataIndex: "operation",
        width: "10%",
        align: "center",
        fixed: "right",
        render: (text, row) => {
          if (isNaN(row.index_no)) {
            return {
              props: {
                colSpan: 0
              }
            };
          } else if (!this.props.transaction_status || this.props.transaction_status === 1) {
            return (
              <div>        
                <Tooltip
                  placement="top"
                  title={<IntlMessages id="detail" />}
                >
                  <Button
                    //style={{ borderRadius: "50%" }}
                    className="invoiceEdtBtn"
                    size="small"
                    onClick={() => this.handleItemDetailModal(row)}
                  >
                    <i className="ion-navicon-round" />
                  </Button>
                </Tooltip>
                <Popconfirm
                  title={<IntlMessages id="confirmDelete" />}
                  onConfirm={() => this.handleDelete(row.index_no)}
                  okText={<IntlMessages id="yes" />}
                  cancelText={<IntlMessages id="no" />}
                >
                  <Tooltip
                    title={<IntlMessages id="delete" />}
                  >
                    <Button
                      //style={{ borderRadius: "50%" }}
                      className="invoiceDltBtn"
                      size="small"
                    >
                      <i className="ion-trash-b" />
                    </Button>
                  </Tooltip>
                </Popconfirm>
              </div>
            );
          } else {
            return (
              <Tooltip
                placement="top"
                title={<IntlMessages id="detail" />}
              >
                <Button
                  //style={{ borderRadius: "50%" }}
                  className="invoiceEdtBtn"
                  size="small"
                  onClick={() => this.handleItemDetailModal(row)}
                >
                  <i className="ion-navicon-round" />
                </Button>
              </Tooltip>
            );
          }
        }
      }
    ];

    // const total = getTotal(dataSource);
    const info = {
      index_no: `${getLanguage({ id: "totalQuantity" })} (${getLanguage({ id: "bsUnt" })})`,
      request_qty: <NumberFormat value={totalQty} />
    };
    const modifiedData = dataSource.length > 0 ? [...dataSource, info] : [];

    return (
      <CardWrapper>
        <div>
          <ModalItemDetail
            visible={visible}
            selectedData={selectedItem}
            handleItemDetailModal={this.handleItemDetailModal}
            handleItemDetail={this.handleItemDetail}
            submit={this.handleItemDetail}
            departments={this.props.departments}
            projects={this.props.projects}
            status={this.props.status}
          />
          <Scrollbars style={{ width: "100%" }}>
            <TableWrapper
              className="invoiceListTable"
              components={components}
              rowKey={record => record.index_no}
              rowClassName={() => "editable-row"}
              dataSource={modifiedData}
              columns={columns}
              pagination={false}
            />
          </Scrollbars>
        </div>
      </CardWrapper>
    );
  }
}

export default EditableTable;
