import React, { Component } from "react";
import { connect } from "react-redux";
import { compose } from "redux";

import ItemRequestCreateForm from "./createForm";

import notification from "../../../../components/notification";
// import { Input, Select, Divider } from "antd";

import LayoutWrapper from "../../../../components/utility/layoutWrapper";
import PageHeader from "../../../../components/utility/pageHeader";
import IntlMessages from "../../../../components/utility/intlMessages";
import Button from "../../../../components/uielements/button";
import actions from "../../../../redux/inventory/itemRequest/actions";
import { Box } from "../../../App/app.style";
import { Tabs } from "antd";

const redirecTo = "/dashboard/inventories/item-requests";
const TabPane = Tabs.TabPane;

class CreateItemRequest extends Component {
  componentDidMount() {
    const { createItemRequest } = this.props;
    createItemRequest();
  }

  componentDidUpdate() {
    // Display error notif
    if (this.props.itemRequest.error) {
      notification("error", this.props.itemRequest.error);
      this.props.clearError();
    }

    if (
      this.props.itemRequest.success &&
      this.props.itemRequest.id_item_request
    ) {
      this.props.history.replace(
        redirecTo + "/update/" + this.props.itemRequest.id_item_request
      );
      // notification("success", this.props.itemRequest.success);
      // this.props.clearSuccess();
    }
  }

  handleSubmit = value => {
    const { storeItemRequest } = this.props;

    // Uppercasing itemRequest code
    // value.customer_type_code = value.customer_type_code.toUpperCase();
    // console.log(value);
    storeItemRequest(value);
  };

  render() {
    // console.log(this.props);
    return (
      <LayoutWrapper>
        <Box>
          <PageHeader title={<IntlMessages id="itemRequests.button.add" />}>
            <Button onClick={() => this.props.history.replace(redirecTo)}>
              <IntlMessages id="button.back" />
            </Button>
          </PageHeader>

          <Tabs defaultActiveKey="1">
            <TabPane tab={<IntlMessages id="jobCostings.tab.detail" />} key="1">
              <ItemRequestCreateForm
                submit={this.handleSubmit}
                itemRequest={this.props.itemRequest}
                history={this.props.history}
              />
            </TabPane>
            <TabPane
              tab={<IntlMessages id="jobCostings.tab.attachment" />}
              key="2"
              disabled
            />
          </Tabs>
        </Box>
      </LayoutWrapper>
    );
  }
}

const mapStateToProps = state => {
  return {
    itemRequest: state.itemRequest,
    auth: state.auth
  };
};

export default compose(connect(mapStateToProps, actions))(CreateItemRequest);
