import React, { Component } from "react";
import moment from "moment";
import { connect } from "react-redux";
import { compose } from "redux";
import actions from "../../../../redux/inventory/itemRequest/actions";
import IntlMessages from "../../../../components/utility/intlMessages";
import Button from "../../../../components/uielements/button";
import {
  Form,
  Input,
  DatePicker,
  Select,
  Divider,
  Spin,
  Row,
  Col,
  Empty,
  Tooltip,
  Icon
} from "antd";
import EditableTable from "./editableTable";
import ModalTransaction from "./modal";
import AddModule from "../../../App/AddModule";

const { Option } = Select;
const { TextArea } = Input;
const InputGroup = Input.Group;

const { getItems } = actions;

class ItemRequestCreateForm extends Component {
  state = {
    id_location: null,
    item: null,
    items: [],
    itemDetails: {
      item_request_details: []
    },
    total_qty: 0,
    search: null,
    visible: false,
    clear: false,
    err: false,
    err_item: false
  };

  componentDidMount() {
    const { user } = this.props;
    const { getItems } = this.props;
    let { default_employee_location } = user ? user : [];
    default_employee_location = default_employee_location
      ? default_employee_location
      : [];

    if (default_employee_location.id_location) {
      getItems(default_employee_location.id_location);
    }
  }

  handleSubmit = e => {
    e.preventDefault();
    this.props.form.validateFieldsAndScroll((err, values) => {
      const { itemDetails } = this.state;

      // validasi item
      if (itemDetails.item_request_details.length <= 0) {
        return this.setState({ err_item: true });
      } else {
        this.setState({ err_item: false });
      }

      if (err) {
        this.setState({ err: true });
        return;
      }

      values.transaction_date = values.transaction_date.format(
        "YYYY-MM-DD HH:mm:ss"
      );
      const data = Object.assign({}, values, itemDetails);
      // console.log(data);
      if (!err) {
        this.setState({ err: false });
        this.props.submit(data);
      }
    });
  };

  handleSelectLocation = id_location => {
    const { getItems } = this.props;
    this.setState({
      id_location: id_location,
      clear: this.state.id_location !== id_location ? true : false
    });

    getItems(id_location);
  };

  handleModal = () => {
    const { visible } = this.state;
    visible
      ? this.setState({ visible: false })
      : this.setState({ visible: true });
  };

  handleSelectItem = value => {
    const { items } = this.props.itemRequest;
    const { search } = this.state;
    const { getFieldValue } = this.props.form;
    const id_location = getFieldValue("id_location");
    const { getItems } = this.props;
    // console.log(items.filter(item => item.id_item === value));
    this.setState({
      item: items.find(item => item.id_item === value)
    });

    if (search) {
      getItems(id_location);
      this.setState({ search: null });
    }
  };

  handleSelectModal = value => {
    const { transactions } = this.props.itemRequest;
    const { setFieldsValue } = this.props.form;
    const transaction = transactions.find(
      curr_data => curr_data.id_item_request === value
    );

    transaction.item_request_details.map(data => {
      data.id_item_request = null;
      data.id_item_request_detail = null;
      data.ordered_qty = 0;
    });

    this.setState({
      items: transaction.item_request_details,
      itemDetails: {
        item_request_details: transaction.item_request_details
      },
      clear: false
    });
    setFieldsValue({ transaction_no: transaction.transaction_no });
    this.handleModal();
  };

  handleSearchItem = value => {
    const { getItems } = this.props;
    const { getFieldValue } = this.props.form;
    const id_location = getFieldValue("id_location");
    // console.log(this.props.purchaseInvoice);
    this.setState({ search: value });
    if (id_location) {
      getItems(id_location, value);
    }
  };

  handleChangeItem = value => {
    this.setState({ search: value });
  };

  handleSaveItemList = (value, total_qty) => {
    this.setState({
      itemDetails: {
        item_request_details: value
      },
      total_qty: total_qty,
      clear: false
    });
  };

  clearItem = (value, total_qty) => {
    this.setState({
      item: null,
      itemDetails: {
        item_request_details: value
      },
      total_qty: total_qty,
      clear: false
    });
  };

  clearItems = (value, total_qty) => {
    this.setState({
      items: [],
      itemDetails: {
        item_request_details: value
      },
      total_qty: total_qty,
      clear: false
    });
  };

  render() {
    // console.log(this.props.itemRequest.transactions);
    const { search, visible } = this.state;
    const { getFieldDecorator, getFieldValue } = this.props.form;
    const { itemRequest } = this.props;
    const { items, locations, departments, projects } = itemRequest
      ? itemRequest
      : [];
    const { user } = this.props;
    let { permissions, default_employee_location } = user ? user : [];
    permissions = permissions ? permissions : [];
    default_employee_location = default_employee_location
      ? default_employee_location
      : [];
    const {
      loading,
      loadingSubmit,
      loadingItems,
      loadingTransactions,
      loadingForeign
    } = itemRequest;
    // const data = itemRequest.itemRequest ? itemRequest.itemRequest : [];

    // const used = data.is_used;
    const error = itemRequest.errorField ? itemRequest.errorField : [];
    // console.log(data);

    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      }
    };
    const formTextArea = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      }
    };
    const tailFormItemLayout = {
      wrapperCol: {
        xs: {
          span: 24,
          offset: 0
        },
        sm: {
          span: 24,
          offset: 0
        }
      }
    };

    return (
      <Form layout="vertical" onSubmit={this.handleSubmit}>
        <ModalTransaction
          visible={visible}
          itemRequest={itemRequest}
          id_location={getFieldValue("id_location")}
          handleModal={this.handleModal}
          handleSelectModal={this.handleSelectModal}
        />

        <Row>
          <Col span={12} style={{ paddingRight: "15px" }}>
            <InputGroup>
              <Col span={12}>
                <Form.Item
                  {...formItemLayout}
                  label={
                    <Tooltip
                      placement="top"
                      title={
                        <IntlMessages id="itemRequests.transactionNo.help" />
                      }
                    >
                      <IntlMessages id="itemRequests.copyPreviousRQ" />
                      <Icon
                        style={{ marginLeft: "5px" }}
                        type="question-circle"
                        theme="filled"
                      />
                    </Tooltip>
                  }
                >
                  <Button
                    disabled={
                      loadingTransactions || !getFieldValue("id_location")
                    }
                    icon="select"
                    loading={loadingTransactions}
                    onClick={this.handleModal}
                    style={{ height: "31px", width: "100%" }}
                    // shape="circle"
                  >
                    <IntlMessages id="button.copy" />
                  </Button>
                </Form.Item>
              </Col>
              <Col span={12}>
                <Form.Item
                  {...formItemLayout}
                  label={<IntlMessages id="itemRequests.transactionDate" />}
                  style={{ marginBottom: 0, paddingBottom: 0 }}
                >
                  {getFieldDecorator("transaction_date", {
                    initialValue: moment()
                  })(
                    <DatePicker
                      style={{ width: "100%" }}
                      showTime={{ format: "HH:mm:ss" }}
                      format="DD MMMM YYYY HH:mm:ss"
                      disabled={
                        !permissions.includes("Can Back Date") ? true : false
                      }
                      allowClear={false}
                      disabledDate={e => e.valueOf() > moment()}
                    />
                  )}
                </Form.Item>
              </Col>
            </InputGroup>

            <Col span={24}>
              <Form.Item
                {...formItemLayout}
                label={<IntlMessages id="itemRequests.location" />}
              >
                {getFieldDecorator("id_location", {
                  initialValue: default_employee_location.id_location,
                  rules: [
                    {
                      required: true,
                      message: <IntlMessages id="required" />
                    }
                  ]
                })(
                  <Select
                    showSearch
                    style={{ width: "100%" }}
                    placeholder={<IntlMessages id="itemRequests.location" />}
                    loading={loadingForeign}
                    onSelect={this.handleSelectLocation}
                    optionFilterProp="children"
                    filterOption={true}
                  >
                    {locations.map((data, key) => (
                      <Option key={key} value={data.id_location}>
                        {`${data.location_code} (${data.location_name})`}
                      </Option>
                    ))}
                  </Select>
                )}
              </Form.Item>
            </Col>
          </Col>
          <Col span={12} style={{ paddingLeft: "15px" }}>
            <Form.Item
              {...formTextArea}
              label={<IntlMessages id="itemRequests.note" />}
            >
              {getFieldDecorator("note", {
                // initialValue: data.note
              })(<TextArea autosize={{ minRows: 4 }} />)}
            </Form.Item>
          </Col>
        </Row>

        <Divider orientation="left" style={{ fontSize: 14 }}>
          <IntlMessages id="itemRequests.subtitle.itemList" />
        </Divider>

        <Row>
          <Col span={24}>
            <Form.Item
              {...formItemLayout}
              validateStatus={
                error.item_request_details || this.state.err_item
                  ? "error"
                  : null
              }
              help={
                error.item_request_details ? (
                  error.item_request_details
                ) : this.state.err_item ? (
                  <IntlMessages id="itemDataEmpty" />
                ) : null
              }
              extra={<IntlMessages id="itemRequests.item.help" />}
            >
              <Select
                value={search ? search : []}
                showSearch
                allowClear
                // optionFilterProp="children"
                filterOption={false}
                placeholder={
                  <IntlMessages id="itemRequests.item.placeholder" />
                }
                loading={loadingItems}
                notFoundContent={
                  loadingItems ? (
                    <Spin size="small" />
                  ) : getFieldValue("id_location") ? (
                    <Empty description={<IntlMessages id="noData" />} />
                  ) : (
                    <Empty
                      description={
                        <IntlMessages id="itemRequests.item.noLocation" />
                      }
                    />
                  )
                }
                onSelect={this.handleSelectItem}
                onSearch={this.handleSearchItem}
                optionFilterProp="children"
                filterOption={true}
                value={undefined}
              >
                {getFieldValue("id_location")
                  ? AddModule(
                      "Create Item",
                      "/dashboard/setup/items/add",
                      "items.pageHeader",
                      this.props.history,
                      permissions
                    )
                  : null}
                {items.map(data => (
                  <Option key={data.id_item} value={data.id_item}>
                    {data.item_code + " - " + data.item_name}
                  </Option>
                ))}
              </Select>
            </Form.Item>
          </Col>
        </Row>

        <Row>
          <Col span={24}>
            <EditableTable
              item={this.state.item}
              items={this.state.items}
              departments={departments}
              projects={projects}
              handleSaveItemList={this.handleSaveItemList}
              clearItem={this.clearItem}
              clearItems={this.clearItems}
              loadingTransactions={loadingTransactions}
              loadingItems={loadingItems}
              clear={this.state.clear}
              status={1}
            />
          </Col>
        </Row>

        <Form.Item {...tailFormItemLayout}>
          <Button
            type="primary"
            htmlType="submit"
            style={{ marginTop: "50px" }}
            loading={loading || loadingSubmit}
          >
            <IntlMessages id="button.submit" />
          </Button>

          <br />

          {this.state.err ? (
            <span style={{ color: "#f5222d" }}>
              <IntlMessages id="form.error" />
            </span>
          ) : null}

          {this.state.err_item ? (
            <IntlMessages id="itemDataEmpty">
              {txt => <span style={{ color: "#f5222d" }}>{txt}</span>}
            </IntlMessages>
          ) : null}
        </Form.Item>
      </Form>
    );
  }
}

const WrappedItemRequestCreateForm = Form.create({
  name: "create_item_request"
})(ItemRequestCreateForm);

const mapStateToProps = state => {
  return {
    user: state.auth.user,
    auth: state.auth
  };
};

export default compose(connect(mapStateToProps, { getItems }))(
  WrappedItemRequestCreateForm
);
